import PropTypes from 'prop-types';

// mui
import { useTheme, } from '@mui/material/styles';
import { Box, Grid, Stack, Typography } from '@mui/material';

// third-party

// project
import SubCard from 'components/cards/SubCard';
import SkeletonTotalOrderCard from 'components/cards/Skeleton/EarningCard';

const CommonCard = ({ isLoading, title = "Title", value = "", percentage = "", icon = () => { }, color = 'primary' }) => {
    const theme = useTheme();
    return (
        <>
            {isLoading ? (
                <SkeletonTotalOrderCard />
            ) : (
                <SubCard sx={{ bgcolor: color }} >
                    <Grid container direction="column">
                        <Grid item>
                            <Grid justifyContent="flex-start" alignItems="center">
                                <Stack direction={`row`} spacing={1}>
                                    <Box sx={{
                                        fontSize: '1rem',
                                        fontWeight: 500,
                                        color: theme.palette.text.default
                                    }}>
                                        {icon}
                                    </Box>

                                    <Typography
                                        sx={{
                                            fontSize: '1rem',
                                            fontWeight: 500,
                                            color: theme.palette.text.default
                                        }}
                                    >
                                        {title}
                                    </Typography>
                                </Stack>
                            </Grid>
                        </Grid>
                        <Grid item>
                            <Typography variant='h1' sx={{
                                fontSize: '2rem',
                                color: theme.palette.text.default,
                                fontWeight: 500, mr: 1, mt: 1.75, mb: 0.75
                            }}>
                                {value}
                            </Typography>
                        </Grid>
                        <Grid item sx={{ mb: 0.75 }}>
                            <Stack direction="row" alignItems={'center'} spacing={1}>
                                <Typography variant='subtitle1' sx={{
                                    fontSize: '1rem',
                                    fontWeight: 500,
                                    color: percentage > 0 ? theme.palette.success.main : theme.palette.error.main
                                }}>{percentage}%</Typography>
                                <Typography
                                    sx={{
                                        fontSize: '1rem',
                                        fontWeight: 500,
                                        color: theme.palette.text.default
                                    }}
                                > From Last Month</Typography>
                            </Stack>
                        </Grid>
                    </Grid>
                </SubCard>
            )}
        </>
    );
};

CommonCard.propTypes = {
    isLoading: PropTypes.bool,
    title: PropTypes.string,
    value: PropTypes.string,
    percentage: PropTypes.string,
    icon: PropTypes.node,
    color: PropTypes.string
};

export default CommonCard;
